import React from 'react';

import { Route, Redirect } from 'react-router-dom';

import style from './App.module.scss';
import Chat from './component/chatComponent/Container/Chat';
import RegistrationFormContainer from './component/authComponent/registration/RegistrationFormContainer';
import AuthFormContainer from './component/authComponent/authForm/AuthFormContainer';

const App = () => {
  return (
    <div className={style.app}>
      <Route exact path="/" render={() => <Redirect to={'auth'} />} />

      <Route path="/auth" render={() =>
        (<div className={style.form__contaier}> <AuthFormContainer /></div>)} />
      <Route path="/registration" render={() =>
        (<div className={style.form__contaier}><RegistrationFormContainer /></div>)} />
      <Route path="/im" render={() =>
        (
          <div className={style.container}>
            <Chat />
          </div>)}
      />

    </div>
  );
}

export default App;
