import React from 'react';
import style from './Messages.module.scss'
import formatDistanceToNow from 'date-fns/formatDistanceToNow'
import ruLocale from 'date-fns/locale/ru'

import avatar from '../../../assets/img/test.png'
import checkedSvg from '../../../assets/img/checked.svg'
import noCheckedSvg from '../../../assets/img/noChecked.svg'

function Messages({ user, text, date, isMe, isReaded, attachments }) {
    return (
        <div className={style.Messages + " " + `${isMe && style.me}`}>

            <div className={style.avatar}>
                <img src={user.avatar || avatar} alt={'avatar ' + user.fullName} />
            </div>

            <div className={style.buble}>
                <div
                    className={style.buble__content + ' ' + `${isMe
                        ? style.white
                        : style.blue}
                    `}>
                    {text}
                </div>
                <div className={style.message__attachments}>
                    {attachments && attachments.map(item => (
                        <img src={item.url} className={style.message__attachment} alt={item.filename} />
                    ))}
                </div>
                <div className={style.date}>{formatDistanceToNow(date, { addSuffix: true, locale: ruLocale })}</div>

            </div>
            {isMe ? isReaded
                ? <img className={style.message__checked} src={checkedSvg} />
                : <img className={style.message__checked} src={noCheckedSvg} />
                : null
            }

        </div>
    )
}



export default Messages;
